'use strict';
import Swiper, { Navigation } from 'swiper';
export const clients = () => {
  Swiper.use([Navigation]);

  var clientsSwiper = new Swiper('.swiper-container', {
    slidesPerView: 1,
    speed: 400,
    spaceBetween: 30,
    centeredSlides: true,
    autoHeight: true,
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
    // pagination: {
    //   el: '.swiper-pagination',
    //   clickable: true,
    // },
    observer: true,
    observeParents: true,
    breakpoints: {
      // when window width is >= 540px
      540: {
        slidesPerView: 2,
      },
      // when window width is >= 768px
      768: {
        slidesPerView: 3,
        centeredSlides: false,
      },
      // when window width is >= 1024px
      1024: {
        slidesPerView: 4,
        centeredSlides: false,
      },
      // when window width is >= 1200px
      1200: {
        slidesPerView: 5,
        centeredSlides: false,
      },
    },
  });
};
